
.app-sidebarblog{position:relative;top:0px;left:0;bottom:0;padding:0px 0 0px;width:auto;min-width:300px;max-width:300px;float:left;max-height:100%;height:calc(100% - 50px);background:#FFF;box-shadow:0 2px 3px rgba(17,17,17,0.1),0 0 0 1px rgba(17,17,17,0.1);overflow-y:auto;overflow-x:hidden
}
.app-sidebarblog .draft{float:right;color:red
}
.app-sidebarblog .postTitle{width:80%;white-space:nowrap;overflow:hidden;text-overflow:ellipsis
}
.app-sidebarblog .tree-view-item-key{padding-right:30px
}
@media screen and (max-width: 768px){
.app-sidebarblog{transform:translate3d(-180px, 0, 0)
}
}
.app-sidebarblog .icon{vertical-align:baseline
}
.app-sidebarblog .icon.is-angle{position:absolute;right:10px;transition:transform .377s ease
}
.app-sidebarblog .icon:not(.is-angle){position:relative;right:3px;top:-1px
}
.app-sidebarblog .menu-label{padding-left:5px
}
.app-sidebarblog .menu-list li a[aria-expanded="true"] .is-angle{-ms-transform:rotate(180deg);transform:rotate(180deg)
}
.app-sidebarblog .menu-list li a+ul{margin:0 10px 0 15px
}
.app-sidebarblog .list h2{font:400 40px/1.5 Helvetica, Verdana, sans-serif;margin:0;padding:0
}
.app-sidebarblog .list ul{list-style-type:none;margin:0;padding:0
}
.app-sidebarblog .list li{font:200 20px/1.5 Helvetica, Verdana, sans-serif;border-bottom:1px solid #ccc
}
.app-sidebarblog .list li:last-child{border:none
}
.app-sidebarblog .list li span{text-decoration:none;color:#000;display:block;width:100%;cursor:pointer;transition:font-size 0.3s ease, background-color 0.3s ease
}
.app-sidebarblog .list li span:hover{background:#f6f6f6
}
.list span{min-height:80px
}
.app-sidebartwo-fixed{position:fixed;top:50px;left:0;bottom:0;padding:20px 0 50px;width:auto;min-width:45px;max-height:100vh;height:calc(100% - 50px);z-index:1023;background:#FFF;box-shadow:0 2px 3px rgba(17,17,17,0.1),0 0 0 1px rgba(17,17,17,0.1);overflow-y:auto;overflow-x:hidden
}
.app-sidebartwo-fixed .tree-view-item-key{padding-right:30px
}
@media screen and (max-width: 768px){
.app-sidebartwo-fixed{transform:translate3d(-180px, 0, 0)
}
}
.app-sidebartwo-fixed .icon{vertical-align:baseline
}
.app-sidebartwo-fixed .icon.is-angle{position:absolute;right:10px;transition:transform .377s ease
}
.app-sidebartwo-fixed .icon:not(.is-angle){position:relative;right:3px;top:-1px
}
.app-sidebartwo-fixed .menu-label{padding-left:5px
}
.app-sidebartwo-fixed .menu-list li a[aria-expanded="true"] .is-angle{-ms-transform:rotate(180deg);transform:rotate(180deg)
}
.app-sidebartwo-fixed .menu-list li a+ul{margin:0 10px 0 15px
}



