
.app-sidebartwo{position:relative;top:0px;left:0;bottom:0;padding:0px 0 0px;width:auto;min-width:45px;float:left;max-height:100%;height:calc(100% - 50px);z-index:1023;background:#FFF;box-shadow:0 2px 3px rgba(17,17,17,0.1),0 0 0 1px rgba(17,17,17,0.1);overflow-y:auto;overflow-x:hidden
}
.app-sidebartwo .tree-view-item-key{padding-right:30px
}
@media screen and (max-width: 768px){
.app-sidebartwo{transform:translate3d(-180px, 0, 0)
}
}
.app-sidebartwo .icon{vertical-align:baseline
}
.app-sidebartwo .icon.is-angle{position:absolute;right:10px;transition:transform .377s ease
}
.app-sidebartwo .icon:not(.is-angle){position:relative;right:3px;top:-1px
}
.app-sidebartwo .menu-label{padding-left:5px
}
.app-sidebartwo .menu-list li a[aria-expanded="true"] .is-angle{-ms-transform:rotate(180deg);transform:rotate(180deg)
}
.app-sidebartwo .menu-list li a+ul{margin:0 10px 0 15px
}
.app-sidebartwo-fixed{position:fixed;top:50px;left:0;bottom:0;padding:20px 0 50px;width:auto;min-width:45px;max-height:100vh;height:calc(100% - 50px);z-index:1023;background:#FFF;box-shadow:0 2px 3px rgba(17,17,17,0.1),0 0 0 1px rgba(17,17,17,0.1);overflow-y:auto;overflow-x:hidden
}
.app-sidebartwo-fixed .tree-view-item-key{padding-right:30px
}
@media screen and (max-width: 768px){
.app-sidebartwo-fixed{transform:translate3d(-180px, 0, 0)
}
}
.app-sidebartwo-fixed .icon{vertical-align:baseline
}
.app-sidebartwo-fixed .icon.is-angle{position:absolute;right:10px;transition:transform .377s ease
}
.app-sidebartwo-fixed .icon:not(.is-angle){position:relative;right:3px;top:-1px
}
.app-sidebartwo-fixed .menu-label{padding-left:5px
}
.app-sidebartwo-fixed .menu-list li a[aria-expanded="true"] .is-angle{-ms-transform:rotate(180deg);transform:rotate(180deg)
}
.app-sidebartwo-fixed .menu-list li a+ul{margin:0 10px 0 15px
}



